import React, { useEffect } from 'react' ;
import * as echarts from 'echarts';

const colorList = ['#CFE547', '#4BCB6F', '#C1752E', '#B12121', '#8247E5', '#432CD6', '#3119CA', '#009FC2']
const arr= [
    {value: 1, name: 'Launchpads'},
    {value: 6, name: 'Seed Round'},
    {value: 8, name: 'Private A'},
    {value: 10, name: 'Private B'},
    {value: 40, name: 'Ecosystem'},
    {value: 20, name: 'Team and Advisors'},
    {value: 10, name: 'Treasury/Community'},
    {value: 5, name: 'Liquidity/MM'},
  ]

function TokenChart() {
  useEffect(() => {
    // 基于准备好的dom，初始化echarts实例
    const myChart = echarts.init(document.getElementById('echartsContentMini') as HTMLElement);
    
    const option = {
      color:colorList,
      tooltip: {
        trigger: 'item'
      },
      series: [{
          type: 'pie',
          center: ['50%', '50%'],
          radius: ['30%', '45%'],
          clockwise: true,
          avoidLabelOverlap: true,
          hoverOffset: 15,
          itemStyle: {
              normal: {
                  color: (params: any) => {
                      return colorList[params.dataIndex]
                  }
              }
          },
          label: {
              show: true,
              position: 'outside',
              // alignTo: 'edge',
              // edgeDistance: '5%',
              formatter: '{a|{b}}：\n{c|{d}%}\n',
              rich: {
                  hr: {
                      backgroundColor: 't',
                      borderRadius: 3,
                      width: 3,
                      height: 3,
                      // padding: [3, 3, 0, -12]
                  },
                  a: {
                      color: '#fff',
                      fontSize: 12,
                      // padding: [10, 80, 10, 17],
                      // borderColor: '#1C191A',
                      // borderWidth: 1,

                  },
                  c: {
                    color: '#fff',
                    fontSize: 14,
                    // padding: [10, 15, 10, 15],
                    // borderColor: '#1C191A',
                    // borderWidth: 1,
                  }
              }
          },
          labelLine: {
              normal: {
                  // length: 50,
                  // length2: 150,
                  lineStyle: {
                      width: 1,
                      type: 'dashed',
                      color: '#fff'
                  }
              }
          },
          data: arr,
      }]
    };
    myChart.setOption(option);
  }, [])
  return <div className='w-full h-full' id='echartsContentMini'>

  </div>;
}

export default TokenChart;
